<template>
  <div class="card">
    <div class="card-body">
      <h3 class="text-dark">
        <strong>Ubah Biodata</strong>
      </h3>
      <br />

      <div class="form-group">
        <label for="inputAddress">Foto</label>
      </div>
      <div class="col-md-12">
        <div class="row">
          <div class="col-md">
            <div class="row">
              <div class="col-2">
                <img
                  class="card-img-top"
                  src="@/assets/img/avatars/avatar.jpg"
                  alt="Unsplash"
                />
              </div>
              <div class="row">
                <div class="col-12">
                  <div class="col-md-12">
                    <input
                      type="file"
                      name="file"
                      id="file"
                      class="inputfile"
                    />
                    <label for="file" class="btn btn-success">Pilih File</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <br />
      <form>
        <div class="form-group">
          <label for="npm">NPM <span class="text-danger">*</span></label>
          <input
            type="text"
            class="form-control"
            id="npm"
            placeholder="NPM"
            v-model="form.bio_npm"
            disabled
          />
        </div>
        <div class="form-group">
          <label for="fullname"
            >Nama Lengkap <span class="text-danger">*</span></label
          >
          <input
            type="text"
            class="form-control"
            id="fullname"
            placeholder="Nama Lengkap"
            v-model="form.bio_full_name"
            disabled
          />
        </div>
        <div class="form-group">
          <label for="email">Email <span class="text-danger">*</span></label>
          <input
            type="text"
            class="form-control"
            id="email"
            placeholder="Email"
            v-model="form.bio_email"
            disabled
          />
        </div>

        <div class="form-group">
          <label for="tempatlahir"
            >Tanggal Lahir<span class="text-danger">*</span></label
          >
          <input
            type="date"
            id="tempatlahir"
            class="form-control datetimepicker-input"
            v-model="form.bio_date_of_birth"
          />
        </div>

        <div class="form-group">
          <label for="birthprovince"
            >Provinsi Lahir <span class="text-danger">*</span></label
          >

          <select
            class="form-control"
            id="birthprovince"
            v-model="form.bio_province_id"
          >
            <option :value="getDataBio.province_id" disabled>
              {{ getDataBio.province }}
            </option>
            <hr />
            <option
              v-for="bio_provinsi in getDataProvinsi"
              :key="bio_provinsi.province_id"
              :value="bio_provinsi.province_id"
            >
              {{ bio_provinsi.province }}
            </option>
          </select>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="birthcity"
              >Kota/Kabupaten Lahir <span class="text-danger">*</span></label
            >
            <select
              id="birthcity"
              class="form-control"
              v-model="form.bio_city_id"
            >
              <option :value="getDataBio.city_id">{{ getDataBio.city }}</option>
              <option
                v-for="kokab in getDataKota"
                :key="kokab.city_id"
                :value="kokab.city_id"
              >
                {{ kokab.city }}
              </option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="tempatlahir"
              >Tempat Lahir<span class="text-danger">*</span></label
            >

            <input
              type="text"
              id="tempatlahir"
              class="form-control datetimepicker-input"
              v-model="form.bio_place_birth"
            />
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputCity"
              >Jenis Kelamin <span class="text-danger">*</span></label
            >
            <select
              id="inputState"
              class="form-control"
              v-model="form.bio_gender"
            >
              <option
                v-if="getDataBio.gender === 'Male'"
                :value="getDataBio.gender"
                disabled
              >
                Laki - Laki
              </option>

              <option
                v-else-if="getDataBio.gender === 'Female'"
                :value="getDataBio.gender"
                disabled
              >
                Perempuan
              </option>

              <option value="Male">Laki - Laki</option>
              <option value="Female">Perempuan</option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="marriage">Status Pernikahan</label>
            <select
              v-model="form.bio_marital_status_id"
              id="marriage"
              class="form-control"
            >
              <option :value="getDataBio.marital_status_id" disabled>
                {{ getDataBio.marital_status }}
              </option>
              <option
                v-for="status in getDataStatus"
                :key="status.marital_status_id"
                :value="status.marital_status_id"
              >
                {{ status.marital_status }}
              </option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="jenisid"
              >Jenis Identitas <span class="text-danger">*</span></label
            >
            <select
              v-model="form.bio_identity_id"
              id="jenisid"
              class="form-control"
            >
              <option :value="getDataBio.identity_id">
                {{ getDataBio.identity_name }}
              </option>
              <option
                v-for="identitas in getDataIdentitas"
                :key="identitas.identity_id"
                :value="identitas.identity_id"
              >
                {{ identitas.identity_name }}
              </option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="identityNumber"
              >Nomor Identitas <span class="text-danger">*</span>
            </label>
            <input
              type="text"
              class="form-control"
              id="identityNumber"
              placeholder="Nomor Identitas"
              v-model="form.bio_identity_number"
            />
          </div>
        </div>
        <div class="form-group">
          <label for="agama">Agama <span class="text-danger">*</span></label>
          <select
            v-model="form.bio_religion_id"
            class="form-control"
            id="agama"
          >
            <option :value="getDataBio.religion_id">
              {{ getDataBio.religion }}
            </option>
            <option
              v-for="agama in getDataAgama"
              :key="agama.religion_id"
              :value="agama.religion_id"
            >
              {{ agama.religion }}
            </option>
          </select>
        </div>
        <div class="form-group">
          <label for="kewarganegaraan"
            >Kewarganegaraan <span class="text-danger">*</span></label
          >
          <select
            class="form-control"
            id="kewarganegaraan"
            v-model="form.bio_citizenship"
            required
          >
            <option :value="getDataBio.citizenship" disabled>
              {{ getDataBio.citizenship }}
            </option>
            <option value="WNI">WNI</option>
            <option value="WNA">WNA</option>
          </select>
        </div>
        <div class="row">
          <div class="col-md-10">
            <div class="form-group">
              <label for="inputAddress">Alamat Tinggal</label>
              <textarea
                type="text"
                class="form-control"
                id="inputAddress"
                placeholder="Alamat Tinggal"
                v-model="form.res_address"
              />
            </div>
          </div>
          <div class="col-md-2">
            <div class="form-row">
              <div class="form-group col-md-12">
                <label for="inputRt">RT/RW</label>
                <div class="row">
                  <div class="col-md-5">
                    <input
                      type="text"
                      class="form-control col-md-12"
                      id="inputRt"
                      placeholder="003"
                      maxlength="3"
                      v-model="form.res_rt"
                    />
                  </div>
                  <div class="col-md-2 form-control">/</div>
                  <div class="col-md-5">
                    <input
                      type="text"
                      class="form-control col-md-12"
                      id="inputRt"
                      placeholder="005"
                      maxlength="3"
                    />
                  </div>
                </div>
              </div>
            </div>

            <div class="form-row">
              <div class="form-group col-md-12">
                <label for="postalCode">Kode Pos</label>
                <div class="row">
                  <div class="col-md-12">
                    <input
                      type="text"
                      class="form-control col-md-12"
                      id="postalCode"
                      placeholder="40375"
                      maxlength="10"
                      v-model="form.res_postal"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputStateRes">Provinsi</label>
            <select
              class="form-control"
              id="inputStateRes"
              v-model="form.res_province_id"
              required
            >
              <option :value="getDataRes.province_id" disabled>
                {{ getDataRes.province }}
              </option>
              <hr />
              <option
                v-for="res_provinsi in getDataProvinsi"
                :key="res_provinsi.province_id"
                :value="res_provinsi.province_id"
              >
                {{ res_provinsi.province }}
              </option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="cityres">Kota/Kabupaten</label>
            <select
              id="cityres"
              class="form-control"
              v-model="form.res_city_id"
            >
              <option :value="getDataRes.city_id">{{ getDataRes.city }}</option>
              <option
                v-for="kokab_res in getDataKota"
                :key="kokab_res.city_id"
                :value="kokab_res.city_id"
              >
                {{ kokab_res.city }}
              </option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputDistrict">Kecamatan</label>
            <select
              id="inputDistrict"
              class="form-control"
              v-model="form.res_district_id"
            >
              <option :value="getDataRes.district_id">
                {{ getDataRes.district }}
              </option>
              <option
                v-for="district_res in getDataKecamatan"
                :key="district_res.district_id"
                :value="district_res.district_id"
              >
                {{ district_res.district }}
              </option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="inputState">Desa/Kelurahan</label>
            <select
              id="inputState"
              class="form-control"
              v-model="form.res_village_id"
            >
              <option disabled v-if="getDataRes.village == null">
                Pilih Desa
              </option>
              <option :value="getDataRes.village_id" disabled v-else>
                {{ getDataRes.village }}
              </option>

              <option
                v-for="village_res in getDataDesa"
                :key="village_res.village_id"
                :value="village_res.village_id"
              >
                {{ village_res.village }}
              </option>
            </select>
          </div>
        </div>

        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputtlp"
              >Telepon <span class="text-danger">*</span></label
            >
            <input
              type="text"
              class="form-control"
              id="inputtlp"
              placeholder="Telepon"
              v-model="form.res_telephone_number"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="inputhp"
              >Nomor HP <span class="text-danger">*</span></label
            >
            <input
              type="text"
              class="form-control"
              id="inputhp"
              placeholder="Nomor HP"
              v-model="form.res_handphone_number"
            />
          </div>
        </div>
        <div class="form-group">
          <label for="statusttl"
            >Status Tempat Tinggal <span class="text-danger">*</span></label
          >
          <select
            class="form-control"
            id="statusttl"
            v-model="form.res_residence_category_id"
          >
            <option disabled :value="getDataRes.residence_category_id">
              {{ getDataRes.residence_category }}
            </option>
            <option
              v-for="tempattinggal in getDataTinggal"
              :key="tempattinggal.res_residence_category_id"
              :value="tempattinggal.category_id"
            >
              {{ tempattinggal.residence_category }}
            </option>
          </select>
        </div>
      </form>
      <button
        class="btn btn-primary btn-success"
        :disabled="disabled"
        @click.prevent="submit"
      >
        SIMPAN
      </button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "DataBiodata",
  inject: ["notyf"],
  
  data() {
    return {
      date: "",
      agama: [],
      identitas: [],
      bio_provinsi: [],
      res_provinsi: [],
      disabled: false,
      kokab: [],
      kokab_res: [],
      tempattinggal: [],
      district_res: [],
      village_res: [],
      status: "",
      val_agama: "",
      form: {
        bio_npm: null,
        bio_email: null,
        bio_full_name: null,
        bio_place_birth: null,
        bio_date_of_birth: null,
        bio_province_id: null,
        bio_city_id: null,
        bio_gender: null,
        bio_marital_status_id: null,
        bio_identity_id: null,
        bio_identity_number: null,
        bio_religion_id: null,
        bio_citizenship: null,
        res_handphone_number: null,
        res_telephone_number: null,
        res_residence_category_id: null,
        res_address: null,
        res_province_id: null,
        res_city_id: null,
        res_district_id: null,
        res_village_id: null,
        res_rt: null,
        res_rw: null,
        res_postal: null,
      },
    };
  },
  computed: {
    ...mapGetters({
      getDataBio: "biodata/databiodata",
      getDataRes: "biodata/dataresidence",
      getDataAgama: "agama/dataagama",
      getDataIdentitas: "identitas/pilihidentitas",
      getDataProvinsi: "provinsi/dataprovinsi",
      getDataKota: "kokab/datakokab",
      getDataKecamatan: "kecamatan/datakec",
      getDataTinggal: "tempattinggal/datatinggal",
      getDataStatus: "stpernikahan/datastatus",
      getDataDesa: "desakelurahan/datades",
    }),
  },

  methods: {
    ...mapActions({
      datamhs: "biodata/biodata",
      dataresi: "biodata/biodata",
      dtagama: "agama/agama",
      dtidentitas: "identitas/identitas",
      dtprovinsi: "provinsi/provinsi",
      dtkokab: "kokab/kokab",
      dttinggal: "tempattinggal/tinggal",
      dtstatus: "stpernikahan/statusnikah",
      dtkecamatan: "kecamatan/kecamatan",
      dtdesa: "desakelurahan/desakelurahan",
    }),
    getBiodatamhs() {
      this.datamhs();
    },
    getResidence() {
      this.dataresi();
    },
    getPilihDesa() {
      this.dtdesa();
    },
    getPilihAgama() {
      this.dtagama();
    },
    getPilihIdentitas() {
      this.dtidentitas();
    },
    getPilihProvinsi() {
      this.dtprovinsi();
    },
    getPilihKotaKab() {
      this.dtkokab();
    },
    getPilihKecamatan() {
      this.dtkecamatan();
    },
    getTempatTinggal() {
      this.dttinggal();
    },
    getStatusNikah() {
      this.dtstatus();
    },

    async submit() {
      try {
        let dataBio = {
          bio_npm: this.form.bio_npm,
          bio_full_name: this.form.bio_full_name,
          bio_email: this.form.bio_email,
          bio_place_birth: this.form.bio_place_birth,
          bio_date_of_birth: this.form.bio_date_of_birth,
          bio_province_id: this.form.bio_province_id,
          bio_city_id: this.form.bio_city_id,
          bio_gender: this.form.bio_gender,
          bio_marital_status_id: this.form.bio_marital_status_id,
          bio_identity_id: this.form.bio_identity_id,
          bio_identity_number: this.form.bio_identity_number,
          bio_religion_id: this.form.bio_religion_id,
          bio_citizenship: this.form.bio_citizenship,
          bio_handphone_number: this.form.res_handphone_number,
          bio_telephone_number: this.form.res_telephone_number,
          res_residence_category_id: this.form.res_residence_category_id,
          res_address: this.form.res_address,
          res_province_id: this.form.res_province_id,
          res_city_id: this.form.res_city_id,
          res_district_id: this.form.res_district_id,
          res_village_id: this.form.res_village_id,
          res_rt: this.form.res_rt,
          res_rw: this.form.res_rw,
          res_postal: this.form.res_postal,
        };
        let response = await axios.put("/oasis/bio", dataBio);
        console.log(dataBio);
        this.getBiodatamhs();
        this.getResidence();
        this.getPilihAgama();
        this.getPilihIdentitas();
        this.getPilihProvinsi();
        this.getPilihKotaKab();
        this.getPilihKecamatan();
        this.getTempatTinggal();
        this.getStatusNikah();
        this.getPilihDesa();
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        return error;
      }
    },
  },
  mounted() {
    this.getBiodatamhs();
    this.getResidence();
    this.getPilihAgama();
    this.getPilihIdentitas();
    this.getPilihProvinsi();
    this.getPilihKotaKab();
    this.getPilihKecamatan();
    this.getPilihDesa();
    this.getTempatTinggal();
    this.getStatusNikah();
    this.form.bio_npm = this.getDataBio.npm;
    this.form.bio_full_name = this.getDataBio.full_name;
    this.form.bio_email = this.getDataBio.email;
    this.form.bio_place_birth = this.getDataBio.place_birth;
    this.form.bio_date_of_birth = this.getDataBio.date_of_birth;
    this.form.bio_identity_number = this.getDataBio.identity_number;
    this.form.bio_citizenship = this.getDataBio.citizenship;
    this.form.bio_gender = this.getDataBio.gender;
    this.form.res_handphone_number = this.getDataRes.handphone_number;
    this.form.res_telephone_number = this.getDataRes.telephone_number;
    this.form.bio_province_id = this.getDataBio.province_id;
    this.form.bio_identity_id = this.getDataBio.identity_id;
    this.form.bio_marital_status_id = this.getDataBio.marital_status_id;
    this.form.bio_religion_id = this.getDataBio.religion_id;
    this.form.bio_city_id = this.getDataBio.city_id;
    this.form.res_address = this.getDataRes.address;
    this.form.res_province_id = this.getDataRes.province_id;
    this.form.res_postal = this.getDataRes.postal;
    this.form.res_rt = this.getDataRes.rt;
    this.form.res_rw = this.getDataRes.rw;
    this.form.res_residence_category_id = this.getDataRes.residence_category_id;
    this.form.res_city_id = this.getDataRes.city_id;
    this.form.res_district_id = this.getDataRes.district_id;
    this.form.res_village_id = this.getDataRes.village_id;
    this.val_agama = this.getDataBio.religion_id;
  },
};
</script>

<style>
.timerCount {
  background: tomato;
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 10px;
  font-size: 20px;
}
.inputfile {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}
</style>